import 'date-fns';
import React from 'react';

import { TextField, Select, Radio, RadioGroup, FormControlLabel } from '@material-ui/core';
import DateFnsUtils from '@date-io/date-fns';
import {
    MuiPickersUtilsProvider,
    KeyboardTimePicker,
    KeyboardDatePicker,
} from '@material-ui/pickers';
import { FieldWrapper } from "./style";

import PhoneInput from 'react-phone-input-2'
import 'react-phone-input-2/lib/style.css'

export const renderTextFieldPrice = ({
    input,
    label,
    type,
    meta: { touched, error, warning }
}) => (
    <FieldWrapper className="field-container">
        <label>{label}</label>
        <TextField {...input} type={type} />
        {touched && error ? <span className="field-error price-erroe">{error}</span> : ''}
    </FieldWrapper>
);

