export const rendernumberField = ({
    input,
    label,
    type,
    meta: { touched, error, warning }
}) => (
    <FieldWrapper className="field-container">
        <label>{label}</label>
        <TextField {...input} type={type} InputProps={{ inputProps: {min: 1} }} />
        {touched && error ? <span className="field-error">{error}</span> : ''}
    </FieldWrapper>
);


 

export const renderDropdown = ({ input, label, options, type, meta: { touched, error }, children }) => {
    return(
        <FieldWrapper className="field-container">
        <label>{label}</label>
        <Select {...input}>
            {options.map((option, index) => (
                <option selected={[{ value: '0', label: "Whole" }]} value={option.value} key={`${option.label}${index}`}>{option.label}</option>
            ))}
        </Select>
        {touched && error ? <span className="field-error">{error}</span> : ''}
    </FieldWrapper>
    )
    
}

