export const renderRadio = ({ input: { onChange, value }, options, label, type, callBackFunction, meta: { touched, error }, children }) => (
    <>
        <FieldWrapper className="field-container">
            <label>{label}</label>
            <RadioGroup name={type} value={value} onChange={onChange}>
                {options.map((option, index) => (
                    <FormControlLabel value={option.value} control={<Radio />} label={option.label} key={`${option.label}${index}`} />
                ))}
            </RadioGroup>
            {touched && error ? <span className="field-error">{error}</span> : ''}
        </FieldWrapper>
    </>
)


export const datePicker = ({ input: { onChange, value }, options, label, type, callBackFunction, meta: { touched, error }, children }) => {
    <>
        <MuiPickersUtilsProvider utils={DateFnsUtils}>
            <KeyboardDatePicker
                disableToolbar
                variant="inline"
                format="MM/dd/yyyy"
                margin="normal"
                id="date-picker-inline"
                label="Date picker inline"
                value={value}
                onChange={onChange}
                KeyboardButtonProps={{
                    'aria-label': 'change date',
                }}
            />
        </MuiPickersUtilsProvider>
    </>
}